@tailwind base;
@tailwind components;
@tailwind utilities;

@keyframes expand {
  from {
    left: var(--start-left);
    top: var(--start-top);
    width: var(--start-width);
    height: var(--start-height);
  }
  to {
    left: 50%;
    top: 50%;
    width: 80%;
    height: 80%;
    transform: translate(-50%, -50%);
  }
}

@keyframes collapse {
  from {
    left: 50%;
    top: 50%;
    width: 80%;
    height: 80%;
    transform: translate(-50%, -50%);
  }
  to {
    left: var(--start-left);
    top: var(--start-top);
    width: var(--start-width);
    height: var(--start-height);
    transform: none;
  }
}

@keyframes fly {
  0% {
    transform: translate(0, 0);
  }
  25% {
    transform: translate(-10px, -10px);
  }
  50% {
    transform: translate(10px, -10px);
  }
  75% {
    transform: translate(-10px, 10px);
  }
  100% {
    transform: translate(0, 0);
  }
}

.animate-fly {
  animation: fly 4s ease-in-out infinite;
}
